import React, { useState, useRef } from 'react';
import axios from 'axios';
import ReCAPTCHA from 'react-google-recaptcha';

const API_URL = "https://portfolio-contact.bcdelacruz.com";
const RECAPTCHA_SITE_KEY = "6LcVS3IqAAAAAD7GpxXvpBQ-_qcSFc0x6BWTN2Yy"; // reCAPTCHA Site Key

const Contact = () => {
    const [formData, setFormData] = useState({ name: '', email: '', message: '' });
    const [responseMessage, setResponseMessage] = useState('');
    const [error, setError] = useState('');
    const [captchaToken, setCaptchaToken] = useState(null);
    const [loading, setLoading] = useState(false);

    const recaptchaRef = useRef(null); // Create a ref for reCAPTCHA

    const handleChange = (e) => {
        setFormData({ ...formData, [e.target.name]: e.target.value });
    };

    const handleCaptchaChange = (token) => {
        setCaptchaToken(token);
    };

    const handleSubmit = async (e) => {
        e.preventDefault();
        setError('');
        setResponseMessage('');
        setLoading(true);

        if (!captchaToken) {
            setError("Please complete the CAPTCHA.");
            setLoading(false);
            return;
        }

        try {
            const response = await axios.post(`${API_URL}/api/contact`, { ...formData, captchaToken });
            setResponseMessage(response.data.message);
            setFormData({ name: '', email: '', message: '' }); // Reset form fields
            setCaptchaToken(null); // Reset CAPTCHA token state
            recaptchaRef.current.reset(); // Reset reCAPTCHA widget
        } catch (err) {
            setError(err.response?.data.message || 'An error occurred while sending the message.');
        } finally {
            setLoading(false); // Reset loading state
        }
    };

    return (
        <div className="container mt-4">
            <h1 className="h2">Send me a Message</h1>
            {responseMessage && <div className="alert alert-success">{responseMessage}</div>}
            {error && <div className="alert alert-danger">{error}</div>}
            <form onSubmit={handleSubmit} className="border p-4 rounded">
                <div className="form-group mb-3">
                    <label htmlFor="name">Name</label>
                    <input
                        type="text"
                        className="form-control"
                        id="name"
                        name="name"
                        value={formData.name}
                        onChange={handleChange}
                        required
                    />
                </div>
                <div className="form-group mb-3">
                    <label htmlFor="email">Email</label>
                    <input
                        type="email"
                        className="form-control"
                        id="email"
                        name="email"
                        value={formData.email}
                        onChange={handleChange}
                        required
                    />
                </div>
                <div className="form-group mb-3">
                    <label htmlFor="message">Message</label>
                    <textarea
                        className="form-control"
                        id="message"
                        name="message"
                        rows="5"
                        value={formData.message}
                        onChange={handleChange}
                        required
                    ></textarea>
                </div>
                
                <div className="form-group mb-3">
                    <ReCAPTCHA
                        ref={recaptchaRef} // Attach ref to reCAPTCHA
                        sitekey={RECAPTCHA_SITE_KEY} // Using the constant for the Site Key
                        onChange={handleCaptchaChange}
                    />
                </div>

                <button type="submit" className="btn btn-primary" disabled={loading}>
                    {loading ? "Sending..." : "Send Message"}
                </button>
            </form>
        </div>
    );
}

export default Contact;
